import { createAction } from 'redux-actions';
import { SIGNIN, SIGNOUT } from '../type/account';

/**
 * Sign In Async Action      .
 */
export const signin = createAction(SIGNIN, (data) => ({
  id: data.id,
  name: data.name,
  type: data.type,
  token: data.token,
}));

/**
 *     
 */
export const signout = createAction(SIGNOUT);
